.about_me {
  display: flex;
  flex-wrap: nowrap;
  grid-area: about_me;
  border-radius: 20px;
  align-items: center;
}

.logo {
  max-width: 40%;
  max-height: 90%;
  display: block;
  object-fit: contain;
  filter: drop-shadow(4px 3px rgba(87, 85, 85, 0.5058823529));
}

.experience {
  grid-area: experience;
  border-radius: 20px;
}

.formation {
  grid-area: formation;
  border-radius: 20px;
}
.formation p {
  padding: 8px;
}

.links {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  grid-area: links;
  border-radius: 20px;
}
.links a {
  display: block;
  color: #153242;
  font-size: 35px;
  margin: 4px;
  text-decoration: none;
}

.galery {
  grid-area: galery;
  border-radius: 20px;
}

.hobbies {
  grid-area: hobbies;
  border-radius: 20px;
}

.learning {
  grid-area: learning;
  border-radius: 20px;
}

.projects {
  grid-area: projects;
  border-radius: 20px;
}

.soft_skills {
  grid-area: soft_skills;
  border-radius: 20px;
}

.tools {
  border-radius: 20px;
  grid-area: tools;
}

.porDefinir {
  grid-area: porDefinir;
  border-radius: 20px;
}

.porDefinir2 {
  grid-area: porDefinir2;
  border-radius: 20px;
}

.grid {
  padding: 16px;
  display: grid;
  grid-template-columns: repeat(7, 100/7vw);
  grid-template-rows: repeat(2, 13vh) auto;
  height: 96.3vh;
  gap: 10px 12px;
